.cardProjeto {
  display: flex;
  flex-direction: column;
  align-items: stretch; /* Permite que a imagem ocupe toda a largura */
  max-width: calc(33.33% - 100px);
  max-height: 35.9rem;
  margin: 0;
  flex-grow: 1;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  transition: transform 0.3s ease;
  position: relative;
}



.cardProjeto:hover::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: -2px;
  left: 0;
  height: 5px;
  background-color: var(--amarelosecondary); /* Cor do hover e ativo para home */
  transition: all 0.3s ease;
}


.cardProjeto-link {
  display: flex;
  flex-direction: column;
  gap: 20px;
  text-decoration: none;
  color: var(--text);
}

.cardProjeto-imagem {
  width: 100%;               
  max-height: 200px;         
  object-fit: fill;         
}

.cardProjeto-texto {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  gap: 20px;
  height: 100%;
  padding: 20px;

}

.cardProjeto-titulo {
  text-align: center;
  font-family: var(--heading);
  font-weight: var(--semi-bold);
  text-decoration: none;
  margin: 0;
  font-size: 1.15rem;
  color: var(--body);

}

.cardProjeto-subtitulo {
  font-style: italic;
  text-decoration: none;
  margin: 0;
  color: var(--body);
  font-size: 1rem;
}

.cardProjeto-descricao {
  text-decoration: none;
  margin: 0;
  color: var(--body);
  text-align: justify;
  font-size: 0.9rem;
  color: rgb(63, 63, 63);
}

@media (max-width: 930px) {
  .cardProjeto {
    display: flex;
    flex-direction: column;
    max-width: calc(50% - 30px);
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  .cardProjeto-imagem {
    min-height: 15rem;
  }
}
@media (max-width: 630px) {
  .cardProjeto {
    display: flex;
    flex-direction: column;
    max-width: 100%;
    border-radius: 20px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  
  }

  .cardProjeto-imagem {
    min-height: 10rem;
  
  }
}

  
